/* Poll results header styling */
div.ui.container h1.ui.header#results-header {
    margin-bottom: 3rem;
}

div.ui.centered.card div.content div.description {
    text-align: center;
}

/* Label container styling */
div.ui.centered.card div.content div.label-container {
    display: flex;
    justify-content: center;
}

/*  Votes label margin styling */
div.ui.centered.card div.content div.label-container div.ui.blue.label {
    margin-top: .5rem;
    margin-bottom: 1rem;
}
